.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 10vh;
  text-align: center;
}

.App-main {
  margin: 10px auto;
  max-width: 500px;
}

.App-main label {
  display: block;
}
.App-main input {
  min-width: 500px;
}
.App-main textarea {
  min-width: 500px;
  min-height: 300px
}